body, html {
  background-color: #f5f5f520;
  height: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.container {
  padding: 20px;
}

h1, h2, h3 {
  margin-top: 0;
}

h3 {
  color: #333;
  border-bottom: 1px solid #e4e4e4;
  margin-top: 0;
  margin-bottom: 5px;
  padding-bottom: 2px;
  font-size: 14px;
}

.btn {
  transition: all .3s;
}

.btn:hover {
  transform: scale(1.05);
}

.btn-upload {
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: all .3s;
  box-shadow: 0 4px 6px #0000001a;
}

.btn-upload:hover {
  background-color: #0056b3;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px #0003;
}

#cy {
  background-color: #e9ecef;
  border: 1px solid #ccc;
  height: 100%;
}

#info, #walks {
  border: 1px solid #ccc;
  flex: auto;
  height: 100%;
  overflow-y: auto;
}

#right-container {
  flex-direction: column;
  height: 100%;
  display: flex;
}

#jsoneditor {
  width: 100%;
  height: 400px;
}

pre.expanded {
  max-height: 500px;
}

header {
  color: #fff;
  background-color: #333;
}

a:hover, .clickable:hover {
  cursor: pointer;
  text-decoration: underline;
}

.element:hover {
  animation: 1s infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.file-upload-container {
  justify-content: flex-start;
}

.igv-container {
  background-color: #f9f9f9;
  border: 1px solid #d6d6d6;
  border-radius: 5px;
  height: 100vh;
  box-shadow: 0 4px 6px #0000001a;
}

#selectedFileName {
  color: #555;
}

.navbar {
  height: 25px;
  font-size: .9rem;
}

.navbar-brand {
  letter-spacing: 1px;
  align-items: center;
  padding-left: 1rem;
  font-size: 1.2rem;
  display: flex;
}

.toolbar-responsive {
  background-color: #fffffff2;
  border-radius: 4px;
  max-height: none;
  padding: 4px;
  transition: max-height .3s;
  box-shadow: 0 2px 5px #0000001a;
}

.toolbar-collapsed {
  max-height: 48px;
  overflow: hidden;
}

.toolbar-responsive::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.toolbar-responsive::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.toolbar-responsive::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 3px;
}

.toolbar-responsive::-webkit-scrollbar-thumb:hover {
  background: #999;
}

.toolbar-responsive .input-group-text, .toolbar-responsive .form-select, .toolbar-responsive .form-control {
  font-size: .9rem;
}

.toolbar-responsive .btn-group, .toolbar-responsive .input-group {
  transition: opacity .2s;
}

@media (width <= 768px) {
  .toolbar-responsive .btn, .toolbar-responsive .input-group-text, .toolbar-responsive .form-select, .toolbar-responsive .form-control {
    padding-left: .5rem;
    padding-right: .5rem;
  }
}

#helpGuideButton {
  position: relative;
}

#helpGuideButton:hover:after {
  content: "Start Interactive Tour";
  color: #fff;
  white-space: nowrap;
  z-index: 1000;
  background-color: #333;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: .8rem;
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
}

.exon {
  cursor: pointer;
  transition: fill .2s;
}

.exon:hover {
  fill: #ff7f0e;
}

.intron {
  pointer-events: none;
}

.exon-label {
  pointer-events: none;
  font-weight: bold;
}

#exonVisualizationContainer svg {
  max-width: 100%;
  overflow: visible;
}

#exonVisualizationModal .modal-lg {
  max-width: 900px;
}

.exon-visualize-btn {
  transition: all .3s;
}

.exon-visualize-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #0000001a;
}

.walks-header {
  border-bottom: 1px solid #e5e5e5;
  margin-bottom: 15px;
  padding: 10px 0;
}

.walks-header h3 {
  align-items: center;
  margin-bottom: 10px;
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
}
